html {
  font-size: 16px;
  @media (max-width: 1199px) {
    font-size: 15px; }
  @media (max-width: 991px) {
    font-size: 14px; }
  @media (max-width: 768px) {
    font-size: 12px; }
  @media (max-width: 567px) {
    font-size: 10px; }
  &.no-scroll {
    overflow: hidden; } }
body {
  font-family: "Roboto", sans-serif; }
ul {
  list-style: none;
  margin: 0;
  padding: 0; }
button {
  cursor: pointer; }
.btn {
  outline: none;
  box-shadow: 0 0 10px 0 #8cf043;
  border: 3px solid #8cef42;
  border-radius: 50px;
  padding: 15px 50px;
  background-color: #b8ff2e;
  color: #243a68;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  transition: all .3s;
  @media (max-width: 576px) {
    font-size: 14px; }
  &.btn_durk:hover {
    color: #243a68; }
  &:hover {
    color: #b8ff2e;
    background: transparent; } }
.section {
  padding: 6rem 0 5.5rem;
  // @media (max-width: 567px)
 }  //   padding: 60px 0
.section-title {
  margin: 0 0 3.75rem;
  color: #fff;
  font-size: 2.5rem;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
