.popup {
  display: none;
  text-align: center;
  padding: 80px 0;
  background: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  overflow-y: auto;
  &.is-open {
    display: block; }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 50px;
    height: 50px;
    padding: 0;
    border: none;
    outline: none;
    background: none;
    &:before, &:after {
      content: '';
      width: 50px;
      height: 2px;
      display: block;
      background: #595959;
      position: absolute;
      top: 24px;
      transition: all 3s; }
    &:before {
      transform: rotate(-45deg); }
    &:after {
      transform: rotate(45deg); }
    &:hover:before, &:hover:after {
      background: #333; }
    &_bot {
      position: relative;
      display: block;
      margin: 45px auto 0;
      right: 0;
      top: 0; } } }
