.repair {
  background: #efeeee;
  &__title {
    color: #333; }
  &-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 -15px; }
  &-item {
    padding: 0 15px;
    margin-bottom: 30px;
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
    @media (max-width: 991px) {
      flex: 0 0 50%;
      max-width: 50%; }
    @media (max-width: 768px) {
      flex: 0 0 100%;
      max-width: 100%; }
    &__link {
      overflow: hidden;
      display: block;
      position: relative;
      margin: 0 auto;
      max-width: 360px;
      &:after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: rgba(255, 156, 0, 0.5);
        transition: all .5s;
        opacity: 0; }
      &:hover:after {
        opacity: 1; } }
    &__img {
      max-width: 360px;
      vertical-align: middle; }
    &__title {
      background: #ffffff;
      color: #333;
      padding: 20px;
      margin: 0 auto;
      height: 75px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-transform: uppercase;
      max-width: 360px;
      text-align: center; } }
  &-desc {
    &__img {
      width: 600px;
      @media (max-width: 768px) {
        width: 70%; }
      @media (max-width: 567px) {
        width: 100%; } }
    &__title {
      text-transform: uppercase;
      font-size: 2rem;
      letter-spacing: 1px;
      font-weight: 400; }
    &__text {
      font-size: 1.5rem;
      text-align: left;
      margin-bottom: 60px;
      padding: 0 20px; } } }
