.process {
  &__title {
    color: #ffa230; }
  &-block {
    display: flex;
    margin: 0 -15px;
    @media (max-width: 991px) {
      flex-wrap: wrap; } }
  &__item {
    position: relative;
    text-align: center;
    padding: 0 15px;
    margin-bottom: 20px;
    flex: 0 0 16.666667%;
    max-width: 16.666667%;
    @media (max-width: 991px) {
      &:nth-child(3n):after {
        content: none; }
      flex: 0 0 33.333333%;
      max-width: 33.333333%; }
    @media (max-width: 567px) {
      &:nth-child(3n):after {
        content: ''; }
      &:nth-child(even):after {
        content: none; }
      flex: 0 0 50%;
      max-width: 50%; }
    &:after {
      content: "";
      width: 42px;
      height: 4px;
      border-radius: 2px;
      background: #4c5463;
      position: absolute;
      top: 47px;
      right: -20px; }
    &:last-child:after {
      content: none; } }
  &-item {
    &__title {
      font-size: 18px;
      font-weight: 500;
      text-transform: uppercase;
      color: #ffa230;
      margin: 31px 0px 10px;
      @media (max-width: 768px) {
        font-size: 16px; } }
    &__desc {
      font-size: 12px;
      color: #3e4757;
      font-weight: 400; } } }
