.form-popup {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 500px;
  margin: 0 auto;
  padding: 0 15px;
  &__title {
    font-size: 1.8rem;
    text-transform: uppercase;
    color: #333; }
  &__input {
    margin: 5vh 0;
    padding: 14px 37px;
    background-color: #a9aeb6;
    border: none;
    text-align: center;
    font-size: 20px;
    max-width: 100%;
    color: #333; } }
