.nav-bar {
  position: fixed;
  width: 100%;
  background: #282828;
  z-index: 3;
  padding: 12px 0;
  &-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  &__nav {
    flex-grow: 1; }
  &__logo {
    height: 44px;
    width: 167px; } }
.burger {
  display: none;
  position: fixed;
  top: 8px;
  right: 15px;
  z-index: 1000;
  background: none;
  border: 0;
  padding: 32px 12px 16px;
  cursor: pointer;
  outline: none;
  border-radius: 50%;
  background: #fff;
  @media (max-width: 768px) {
    display: block; }
  &__layers {
    position: relative;
    display: block;
    width: 28px;
    height: 4px;
    margin: 0 auto;
    background: #ffa521;
    transform: skew(5deg);
    transition: all .275s;
    .is-open & {
      transform: skew(5deg) translateY(-8px) rotate(-45deg);
      background: #5A3B5D;
      &:after, &:before {
        background: #5A3B5D; } }
    &:after, &:before {
      content: '';
      display: block;
      height: 100%;
      background: #ffa521;
      transition: all .275s; }
    &:after {
      transform: translateY(-12px) translateX(-2px) skew(-20deg);
      .is-open & {
        transform: translateY(-12px) translateX(10px) skew(-20deg);
        opacity: 0; } }
    &:before {
      transform: translateY(-16px) skew(-10deg);
      .is-open & {
        transform: translateY(0px) skew(-10deg) rotate(75deg); } } } }
.nav {
  &:before {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: -100px;
    background: #ffaf09;
    transform-origin: 0 0;
    transform: skew(90deg) translateX(0%);
    transition: all .275s; }
  &.is-open {
    opacity: 1;
    z-index: 100;
    visibility: visible;
    overflow: hidden;
    &:before {
      transform: skew(15deg) translateX(-9%); } }
  @media (max-width: 768px) {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    background: #FFF;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    transition: all .375s; }
  &-menu {
    padding: 0 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    @media (max-width: 768px) {
      position: inherit;
      top: 0;
      right: 0;
      display: flex;
      flex-direction: column;
      height: 93%;
      justify-content: flex-start;
      align-items: flex-end;
      padding: 20px 0;
      transform: skew(15deg) translateX(-20%); }
    &__item {}
    &__link {
      color: #ffa200;
      font-size: 16px;
      font-weight: 400;
      text-transform: uppercase;
      text-decoration: none;
      text-align: center;
      display: block;
      transition: all .3s;
      &:hover {
        color: #fff; }
      @media (max-width: 991px) {
        font-size: 12px; }
      @media (max-width: 768px) {
        background: wheat;
        margin-bottom: 10px;
        font-size: 20px;
        color: #5A3B5D;
        // transform: skew(-15deg)
        padding: 3vh 20px; } } } }
.phones {
  @media (max-width: 768px) {
    display: none; }
  &__item {
    display: block;
    color: #ffffff;
    font-family: "Roboto Slab";
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 0.2px;
    white-space: nowrap;
    @media (max-width: 1199px) {
      font-size: 16px; } } }
