.advantages {
  &-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 80%;
    margin: 0 auto;
    @media (max-width: 991px) {
      width: 100%; } }
  &__item {
    text-align: center;
    padding: 0 15px;
    margin-bottom: 20px;
    flex: 0 0 33.3333%;
    max-width: 33.3333%;
    @media (max-width: 991px) {
      flex: 0 0 50%;
      max-width: 50%; } }
  &__img {
    @media (max-width: 400px) {
      width: 100%; } }
  &__title {
    color: #333;
    font-size: 1.6rem;
    font-weight: 500;
    margin: 20px 0 14px; }
  &__desc {
    color: #3e4757;
    font-size: 14px; } }
