.hero {
  &-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around; }
  &__title {
    margin: 55px 0;
    color: #ffffff;
    font-size: 40px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.45px;
    text-align: center;
    @media (max-width: 991px) {
      font-size: 35px; }
    @media (max-width: 768px) {
      font-size: 28px; }
    @media (max-width: 567px) {
      font-size: 20px;
      margin: 30px 0; } }
  &__desc {
    width: 80%;
    margin: 77px 0 95px;
    @media (max-width: 1199px) {
      margin: 15px 0 60px; } }
  &__phone-extra {
    margin: 77px 0;
    @media (max-width: 576px) {
      margin: 40px 0; } } }

.hero-desc {
  line-height: 1.7em;
  // @media (max-width: 991px)
  //   line-height: 1.3em
  // @media (max-width: 768px)
  //   line-height: 1em
  &__item {
    color: #ffffff;
    font-size: 22px;
    font-weight: 700;
    letter-spacing: -0.31px;
    padding-left: 30px;
    position: relative;
    @media (max-width: 1199px) {
      font-size: 18px; }
    @media (max-width: 991px) {
      font-size: 16px; }
    @media (max-width: 768px) {
      font-size: 14px; }
    &:before {
      content: '';
      display: block;
      background: url(../img/checkmark.png) center 0 no-repeat;
      background-size: contain;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 25px;
      @media (max-width: 991px) {
        width: 20px; }
      @media (max-width: 768px) {
        width: 15px; } } } }

.phone-extra {
  &__link {
    display: block;
    color: #ffa200;
    font-family: "Roboto Slab";
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 0.16px;
    text-decoration: none;
    @media (max-width: 576px) {
      font-size: 24px; }
    &:hover {
      text-decoration: underline; } } }
