.why-we {
  background: #333 url("../img/why-we-bg.jpg") center no-repeat;
  background-size: cover;
  &-block {
    width: 65%;
    margin: auto;
    @media (max-width: 567px) {
      width: 100%; } }
  &__item {
    margin-bottom: 44px;
    &:last-child {
      margin-bottom: 0; } }
  &-item {
    padding-left: 28px;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      width: 20px;
      height: 20px;
      top: 4px;
      left: 0;
      background-color: #ff9c00; }
    &__title {
      color: #ffffff;
      font-family: "Roboto Slab";
      font-size: 1.5rem;
      font-weight: bold;
      text-transform: uppercase;
      margin: 0 0 12px; }
    &__desc {
      color: #ffffff;
      font-size: 14px;
      font-weight: 400;
      line-height: 1.45;
      margin: 0; }
    &-list {
      &__item {
        position: relative;
        padding-left: 17px;
        &:before {
          content: '';
          position: absolute;
          top: 4px;
          left: 0;
          width: 12px;
          height: 12px;
          background-color: #ff9c00; } } } } }
