.header {
  background: #333 url(../img/header-bg.jpg) 100% 0 no-repeat;
  background-size: cover;
  position: relative;
  &:after {
    // content: ''
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    background: #333;
    opacity: .7; }
  @media (max-width: 991px) {
    background-position: 66% 0; }
  &__nav-bar {}
  &__hero {
    position: relative;
    z-index: 2;
    padding-top: 60px; } }
