.application {
  background: #fff url(../img/form-bg.jpg) 0 0 no-repeat;
  background-size: cover;
  &__title {
    &_size_s {
      font-size: .6em;
      display: block;
      margin-top: .5em;
      margin-bottom: -1.5em; }
    &_color_green {
      color: #b8ff2e;
      font-size: .9em; } }
  &-form {
    &-wrap {
      display: flex;
      flex-direction: column;
      align-items: center; }
    &__item {
      width: 50%;
      margin-bottom: 20px;
      @media (max-width: 768px) {
        width: 380px; }
      @media (max-width: 567px) {
        width: 100%; } }
    &__label {
      color: #fff;
      margin-bottom: 5px;
      display: block;
      font-size: 16px; }
    &__input {
      width: 100%;
      padding: 0 37px;
      background-color: #878d97;
      border: none;
      display: block;
      font-size: 16px;
      line-height: 40px;
      color: #333; }
    &__btn {
      margin-top: 80px;
      @media (max-width: 567px) {
        margin-top: 40px; } } } }
