.feedback {
  background: #333 url("../img/feedback-bg.jpg") center no-repeat;
  background-size: cover;
  &-block {
    display: flex;
    width: 960px;
    margin: auto;
    flex-wrap: wrap;
    @media (max-width: 1199px) {
      width: auto; } }
  &__item {
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
    padding: 0 1.4rem;
    margin-bottom: 2rem;
    @media (max-width: 991px) {
      flex: 0 0 50%;
      max-width: 50%; }
    @media (max-width: 567px) {
      flex: 0 0 100%;
      max-width: 100%; } }
  &-item {
    &-wrap {
      background: rgba(18, 18, 24, 0.54);
      border-radius: 15px;
      padding: 30px 20px;
      height: 100%; }
    &__title {
      color: #ffffff;
      font-size: 1.5rem;
      font-weight: 500;
      text-align: center;
      margin-top: 0; }
    &__desc {
      color: #ffffff;
      font-size: 14px;
      line-height: 20px; } }
  &__more {
    margin-top: 50px; }
  &-more {
    text-align: center;
    &__link {
      color: #ffffff;
      font-size: 14px;
      text-decoration: underline;
      &:hover {
        text-decoration: none; } } }
  &__btn-block {
    margin-top: 50px;
    padding: 20px 0; }
  &-btn-block {
    text-align: center; } }
